import axios from 'axios'
//const baseUrl = 'https://manage-items.herokuapp.com/api/items'
const baseUrl = '/api/items'

let token = null

const getAll = async () => {
  const config = {
    headers: { 'Authorization': token }
  }
  const response = await axios.get(baseUrl, config)
  return response.data
}

const setToken = newToken => {
  token = `bearer ${newToken}`
  return token
}

const create = async (newObject) => {
  const config = {
    headers: { 'Authorization': token }
  }
  /*return axios.post(baseUrl, newObject, config)
  }*/
  const response = await axios.post(baseUrl, newObject, config)
  return response.data
}

const updateUse = async (id, newUse) => {
  console.log(token)
  const config = {
    headers: { 'Authorization': token }
  }

    /*const response = await axios.post(`${baseUrl}/${id}/usage`, newObject, config)
  }*/
  const response = await axios.post(`${baseUrl}/${id}/use`, newUse, config)

  return response.data
}

const update = (id, newObject) => {
  const config = {
    headers: { 'Authorization': token }
  }

  return axios.put(`${baseUrl}/${id}`, newObject, config).then(response => response.data)
}

const remove = (id) => {
  const config = {
    headers: { 'Authorization': token }
  }
  return axios.delete(`${baseUrl}/${id}`, config)
}

export default { getAll, create, update, remove, updateUse, setToken }
