import logo from './logo.svg';
import './App.css';

import React, {useEffect} from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { BrowserRouter as Router, Switch, Route, Link, NavLink } from "react-router-dom"

import {initializeItems,clearItems} from './reducers/ItemReducer'
import {logout} from './reducers/LoginReducer'

import itemService from './services/items';

import Item from './components/Item';
import ItemList from './components/ItemList';
import ItemForm from './components/ItemForm';

import LoginForm from './components/LoginForm'

const App = () => {

  const dispatch = useDispatch()

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("loggedUser"))
    if (user) {
      itemService.setToken(user.token)
      //itemService.getAll().then(items=>dispatch(initializeItems(items)))
    }
  }, [])

  useEffect(() => {
    if (user) {
      itemService.getAll().then(items=>dispatch(initializeItems(items)))
    }
  }, [])


  const user = useSelector(state => state.loggedUser)
  const items = useSelector(state => state.items)

  const itemById = (id) =>
    (items.find(i => i.id === id))

  const LoginStatus = () => {
    const logoutButton = {display: user === null ? 'none' : '', backgroundColor: "darkGrey", color:"black", padding:5, borderRadius:5, cursor: "pointer",float:"right", marginRight:"25px"}
    return (
      <div>
        <p>
          <a style={{float:"left", marginLeft:"25px"}}>Logged in as {user.name}</a>
          <a style={logoutButton} onClick={handleLogout}>logout</a>
        </p>
        <br/>
      </div>
    )
  }

  const handleLogout = async(e) => {
    await itemService.setToken(null)
    await localStorage.removeItem('loggedUser')
    dispatch(logout())
    dispatch(clearItems())

  }
/*<header className="App-header">
  <Link to="/"><img src={logo} className="App-logo" alt="logo" /></Link>
</header>*/
  return (
    <div className="App">
      <Router>
        <div>
        <Link to="/"><p>[ FRONTPAGE ]</p></Link>
        {user !== null && <LoginStatus/>}
        {(user === undefined || user === null) && <LoginForm/>}
        <Switch>
          <Route exact path="/"><ItemList/></Route>
          <Route exact path="/items/:id"><Item items={items}/></Route>
        </Switch>
        {user !== null && <ItemForm/>}
      </div>
      </Router>
    </div>
  )
}

export default App
