import React from 'react'
import { useParams } from "react-router-dom"


const Item = ({items}) => {
  const id = useParams().id
  const item = items.find(i=> i.id ===id)
  if (item === null || item === undefined || item.use === undefined) {
    return <div></div>
  }

  const totalUse = item.use.length === 0 ? 0 : item.use.map(use=>use.amount).reduce((a,b)=>a+b)

  return(
    <div>
      <h2>{item.name}</h2>
      <div>
        <p>In total you have used this item for <a>{totalUse} {item.units}</a></p>
      </div>
    </div>
  )
  }


