
const itemReducer = (state = [], action) => {
  switch(action.type) {
    case 'INIT_ITEMS':
      return action.data
    case 'UPDATE':
      const oldItems = state.filter(item=> item.id !== action.data.id)
      const updatedItem = action.data
      return [...oldItems, updatedItem]
    case 'REMOVE':
      const existingItems = state.filter(item=> item.id !== action.data.id)
      return existingItems
    case 'ADD_NEW':
      const newItem = action.data
    return [...state, newItem]
    case 'CLEAR_ITEMS':
    return []
    default:
      return state
  }
}

export const initializeItems = (items) => {
  return {
      type: 'INIT_ITEMS',
      data: items
  }
}

export const createItem = (item) => {
  return {
      type: 'ADD_NEW',
      data: item
  }
}

export const addUse = (item, amount) => {
  return {
    type: 'UPDATE',
    data: item
  }
}

export const removeItem = (item) => {
  return {
    type: 'REMOVE',
    data: item
  }
}

export const clearItems = () => {
  return {
    type: 'CLEAR_ITEMS'
  }
}
export default itemReducer
