import React, {useState} from 'react'
import itemService from '../services/items'
import {addUse} from './../reducers/ItemReducer'
import { useDispatch } from 'react-redux'

const ItemListObject = (props) => {

  const dispatch = useDispatch()
  const item = props.item
  const [minimize, setMinimize] = useState(true)
  const hideDetails = { display: minimize ? 'none' : '' }

  const toggleDetailVisibility = () => {
      setMinimize(!minimize)
    }
  function formatToDateAndTime(unformattedDate) {
    let dateAndTime = unformattedDate.split('T')
    dateAndTime[1] = dateAndTime[1].split('.')[0]
    return dateAndTime
  }

  const total_use = item.use.length === 0 ? 0 : item.use.map(use=>use.amount).reduce((a,b)=>a+b)
  const previously_used = item.use.length === 0 ? 'This item hasn\'t been used' : `previously used ${formatToDateAndTime(item.use[item.use.length-1].date)[0]}`
  const mockButton =  {backgroundColor: "grey", color:"white", textDecoration:"none",borderRadius: 5, fontSize:18, padding:6, cursor: "pointer"}

  const handleUseIncrease = (item, amount) => {
    const newUse = {"date": new Date().toISOString(),"amount":Number(amount)}
    if (window.confirm(`Add ${amount}${item.units} for ${item.name}?`)) {
      itemService.updateUse(item.id, newUse).then(item=>dispatch(addUse(item, amount)))
    } else {
      console.log("nothing added")
    }

  }

  return (
    <div onClick={toggleDetailVisibility}>
      <div className='list-item-mini-info'>
        <p style={{marginBottom:2, cursor: "pointer"}}>{props.item.name}</p>
      </div>
      <div className='list-item-detail-info' style={hideDetails}>
        <p style={{fontSize:13, marginBottom:1,marginTop:1}}>{previously_used}</p>
        <p style={{fontSize:13, marginBottom:1,marginTop:1}}>in total used for {total_use} {item.units}</p>
        <p><a style={mockButton} href={`/items/${item.id}`}>Show more</a></p>
        <p style={{fontSize:15, marginBottom:2}}>log new use</p>
        {item.common_usecases.map(usecase=>
        <button key={usecase} className='usecase-log-button' onClick={() => handleUseIncrease(item, usecase)}>{usecase} {item.units}</button>
      )}
      </div>
    </div>

  )
}

export default ItemListObject
